import BallsHolder from './components/BallsHolder';
import SunkHolder from './components/SunkHolder';
import React, { createContext, useRef, useState } from "react";
import { BrowserRouter } from 'react-router-dom';
import StartingSpots from './Spots.json';
import StartingStripes from './Stripes.json';
import { SportsTennis } from '@material-ui/icons';
export const GameContext = createContext(null);

function App() {

  const [balls, setBalls] = useState(StartingSpots);
  const [gameState, setGameState] = useState("game started");
  const [states, setStates] = useState([])

  const advance = (game) => {
    setStates([...states, JSON.stringify(
      {
        balls: game.balls,
        gameState: game.gameState,
      })])
  }

  const game = {

    balls,
    setBalls,
    setGameState,
    advance,
  }

  function reset() {
    game.balls.forEach(b => {
      b.active = true
  })
  game.advance(game)
  }

  function stripes() {
    setBalls(StartingStripes)
    reset()
  }

  function spots() {
    setBalls(StartingSpots)
    reset()
  }


  return (
      <BrowserRouter basename="/tourneyballs">
        <GameContext.Provider value={game}>
          <div className="App">
            <main>
              <div><button onClick={reset}>reset</button>
              <button onClick={spots}>spots</button>
              <button onClick={stripes}>stripes</button>  </div>
              <div>
                <BallsHolder />
              </div>
            </main>
          </div>
        </GameContext.Provider>
      </BrowserRouter>
  )
}


export default App;
